import './todo.scss'
import brush from '../todo/trash.svg'
console.log(brush)

function Todo({todo, key, deleteElem, id, check, isComplated}){
    
    return(
        <>
        <li className="item" key={key}> <input className="checkbox" defaultChecked={isComplated} data-id={id} type="checkbox" onChange={check} /> <p className="text">{todo}</p>
        <button className="btn far fa-trash-alt" data-id={id} onClick={deleteElem} ></button> </li>
        </>
        )
    }
    
    export default Todo