import {useState} from "react";
import Todo from "./components/todo/todo";
import content from "./components/lang/context";
import './App.scss'

function App() {

  const [lang, setlang] = useState('uz')
  
  let [todo, setTodo] = useState(JSON.parse(window.localStorage.getItem('todo')) || [])
  
  function deleteElem(e){
    const id = e.target.dataset.id
    
    let findTodo = todo.filter(t => t.id !== Number(id))
    
    setTodo(findTodo)
  }
  
  function handle(e){
    if(e.key === 'Enter'){
      let newTodo = {
        id: new Date().getTime(),
        content: e.target.value.trim(),
        isComplated: false
      }
      
      setTodo([newTodo, ...todo])
      
      e.target.value = null
    }
  }
  
  function check(e){
    const checkId = e.target.dataset.id
    
    let findCheck = todo.find(t => t.id === Number(checkId))
    findCheck.isComplated = !findCheck.isComplated
    
    window.localStorage.setItem('todo', JSON.stringify(todo))
  }
  
  return (
    <div className="wrapper">

    <div className="header">
      <p></p>
    <h1>{content[lang].greetings}</h1>
    <select className="select" onChange={(e) =>{
      setlang(e.target.value)
    }}>
    <option value="uz">UZ</option>
    <option value="ru">RU</option>
    <option value="eng">ENG</option>
    </select>
    </div>

    <div className="list">
    <input className="input" type="text" onKeyPress={handle}/>
    
    {window.localStorage.setItem('todo', JSON.stringify(todo))}
    
    <ul>
    {todo.map(t => <Todo key={t.id} todo={t.content} id={t.id} isComplated={t.isComplated} check={check} deleteElem={deleteElem}/>)}
    </ul>
    </div>
    
    
    </div>
    );
  }
  
  
  export default App;
  